
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import { mapState , mapGetters} from "vuex";
export default {
name:"topiclist",
props:['name'],
data() {
return {
listData:[],   //
page:1,  //
limit:20, //
pullUpConfig:{  //
content: '',
downContent: '',
upContent: '',
loadingContent: '...'
},
pullDownConfig:{
content: '',
downContent: '',
upContent: '',
loadingContent: '...'
}
}
},
computed:{
...mapState(['topiclists']),
...mapGetters(['fileterTab'])
},
components:{
Tab,
TabItem,
Cell,
Group,
Scroller
},
methods: {

handelTab(index){
console.log(index);

this.$router.push("/topiclist/"+ this.topiclists[index].name)
},

refreshData(){
var app = this;
this.$http.get("/topics",{
params:{
tab:this.name,
page:1,
limit:this.limit
}
}).then(function(response){
if(response.data.success){
app.listData = response.data.data;

app.$refs.pullup.donePulldown();
app.page = 1;
}
})

},

loadMoreData(){
var app = this;
this.page = this.page + 1;
this.$http.get("/topics",{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(response){
if(response.data.success){
for(var i=0;i<response.data.data.length;i++){
app.listData.push(response.data.data[i]);
}

app.$refs.pullup.donePullup()
}
})
}
},


beforeRouteEnter (to, from, next) {

next(function(vm){
vm.$http.get("/topics",{
params:{
tab:to.params.name,
page:vm.page,
limit:vm.limit
}
}).then(function(response){
console.log(response);
if(response.data.success){
vm.listData = response.data.data;
}
})
})
},

beforeRouteUpdate(to,from,next){
var app = this;
this.$http.get("/topics",{
params:{
tab:to.params.name,
page:this.page,
limit:this.limit
}
}).then(function(response){
console.log(response);
if(response.data.success){
app.listData = response.data.data;
}
})
next(); 
}
}



