
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css"
import axios from "axios";
export default {
name:"create",
components:{
Group,
XTextarea,
XButton,
mavonEditor
},
data() {
return {
title:'',   //
content:'',  //
toolbars:{
bold: true, // 
italic: true, // 
header: true, // 
underline: true, // 
ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 
code: true, // code
table: true, // 
htmlcode: true, // html
help: true, // 
/* 2.1.8 */
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
/* 2.2.1 */
subfield: true, // 
preview: true, // 
}
}
},
methods: {

uploadImg(pos,file){


console.log(pos)
console.log(file);
var app = this;
var params = new FormData();
params.append("smfile",file)

axios.post("https://sm.ms/api/v2/upload",params,{
headers:{
'Content-Type': 'multipart/form-data',
'Authorization': 'hJiJFIx3XQtUrT4xgptBm4c04GaDBUZ5'
}
}).then(function(res){
console.log(res);

app.$refs.imgUpload.$img2Url(pos,res.data.url)
})


},
handleTopic(){
var app = this;
this.$http.post("/topics",{
accesstoken: this.$store.state.accesstoken,
tab:"dev",
title:this.title,
content:this.content
}).then(function(res){
console.log(res.data);
if(res.data.success){

app.$router.push("/topiclist/dev");
}
})
}
},
}



