import Vue from "vue";
import Vuex from "vuex";
import mutations from "./mutations";
import actions from "./actions";

Vue.use(Vuex);

const store = new Vuex.Store({
    state:{
        topiclists:[
            {name:"all",desc:""},
            {name:"good",desc:""},
            {name:"share",desc:""},
            {name:"ask",desc:""},
            {name:"job",desc:""},
            {name:"dev",desc:""},
          ],
          isShow:false,  //
          isLogined:false, //
          //
          loginedMenus:{
            create:"",
            center:"",
            msgs:"",
            logout:""
          },
          //
          unLoginMenu:{
            login:""
          },
          loginname:"",  // 
          author_id:"",   //id
          accesstoken:"",  //
          msgCount:""     //
    
    },
    //
    getters:{
        //
        moreMenus(state){
            //isLogined=true
            if(state.isLogined){
              return state.loginedMenus; // 
            }
            return state.unLoginMenu;  //
        },
        //
        fileterTab(state){
            return function(tab){
                for (let i = 0; i < state.topiclists.length; i++) {
                  const element = state.topiclists[i];
                  if( element.name == tab ){
                      return state.topiclists[i].desc;
                  }
                }
            }
        }
    },
    mutations,
    actions
});

export default store;


